import Bus from '@/plugin/bus'
import {BusName} from "@/Constant";

let Trim = {
  all(str) {
    return str.replace(/(^\s*)|(\s*$)/g, "");
  },
  left(str) {
    return str.replace(/(^\s*)/g, "");
  },
  right(str) {
    return str.replace(/(\s*$)/g, "");
  }
}
/**
 * showToasttrue
 */
export default {
  Trim: Trim,
  trim(val, type = '', showToast = false) {
    let msg;
    if (Trim.all(val) == '') {
      msg = `${type}`
    }
    if (showToast && msg) {
      Bus.$emit(BusName.showToast, msg)
    }
    return msg
  },
  /**
   * 
   */
  tel(val, showToast = false) {
    let msg;
    var reg = /^[1][3,4,5,7,8][0-9]{9}$/;
    if (Trim.all(val) == '') {
      msg = ''
    }
    else if (val.length != 11) {
      msg = '11'
    }
    else if (!reg.test(val)) {
      msg = ''
    }
    else {

    }
    if (showToast && msg) {
      Bus.$emit(BusName.showToast, msg)
    }
    return msg
  },

  /**
   * 
   */
  name(val, showToast = false) {
    let msg;
    let reg = /[\u4E00-\u9FA5]{2,5}(?:[\u4E00-\u9FA5]{2,5})*/
    if (Trim.all(val) == '') {
      msg = ''
    }
    else if (!reg.test(val)) {
      msg = '25'
    }
    else {

    }
    if (showToast && msg) {
      Bus.$emit(BusName.showToast, msg)
    }
    return msg
  },
  /**
   * 
   */
  idNumber(val, showToast = false) {
    let msg;
    let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X)$)/
    if (Trim.all(val) == '') {
      msg = ''
    }
    else if (!reg.test(val)) {
      msg = ''
    }
    else {
    }
    if (showToast && msg) {
      Bus.$emit(BusName.showToast, msg)
    }
    return msg
  },
  /**
   *   
   */
  payPassLen(len, showToast = false) {
    let msg;
    if (len != 6) {
      msg = ''
    }
    if (showToast && msg) {
      Bus.$emit(BusName.showToast, msg)
    }
    return msg
  },
  /**
   *   8-20
   * 
   */
  loginPassLen(len, showToast = false) {
    let msg;
    if (len < 8 || len > 20) {
      msg = ''
    }
    if (showToast && msg) {
      Bus.$emit(BusName.showToast, msg)
    }
    return msg
  }
}


