/**
 * ORG_ID 
 */

export const ORG_ID_NUM = {
  JinShang: '70',
  ZhengZhou: '49',
  ZhongBang: '227'
}

export const CheckBank = function (val) {
  let arr = []
  Object.keys(ORG_ID_NUM).forEach(function (key) {
    arr.push(ORG_ID_NUM[key])
  });
  return arr.indexOf(val) != '-1';
}



